import React from "react";
import Publisher from "./PublisherList";
import {styleB, styleIB} from "./Styles";

interface IBook {
    id: number,
    title: string,
    author: string,
    workType: string,
    about: React.ReactElement,
    imgUrl: string,
}


function returnLinkToBookstore(url: string, publisher: string): JSX.Element {
    return (
        <>
            <a
                href={url}
                style={styleB}
                target="_blank"
                rel="noreferrer"
            >
                {publisher}
            </a>
        </>
    );
}

